<template>
<main>
    <ul class="live-btn">
        <li><img src="../../assets/img/live1.png" ></li>
        <li><img src="../../assets/img/live2.png" ></li>
        <li><img src="../../assets/img/live3.png" ></li>
        <li><img src="../../assets/img/live4.png" ></li>
        <li><img src="../../assets/img/live5.png" ></li>
        <li><img src="../../assets/img/live6.png" ></li>
        <li><img src="../../assets/img/live7.png" ></li>
        <li><img src="../../assets/img/live8.png" ></li>
    </ul>
    <!-- 电台 -->
    <article class="live-list">
        <div class="header">
            <h5>电台</h5>
            <p>进去看看</p>
        </div>
        <ul>
            <li v-for="dt in livedt" :key="dt.id">
                <h3><img :src="dt.src"></h3>
                <p>
                    <span>{{dt.zz}}</span>
                    <span>{{dt.people}}</span>
                </p>
                <h5>{{dt.tit}}</h5>
            </li>
        </ul>
    </article>
    <!-- artif -->
    <article class="live-list">
        <div class="header">
            <h5>ARTIFACT</h5>
            <p>进去看看</p>
        </div>
        <ul>
            <li v-for="ar in livear" :key="ar.id">
                <h3><img :src="ar.src"></h3>
                <p>
                    <span>{{ar.zz}}</span>
                    <span>{{ar.people}}</span>
                </p>
                <h5>{{ar.tit}}</h5>
            </li>
        </ul>
    </article>
    <!-- 荒野 -->
    <article class="live-list">
        <div class="header">
            <h5>荒野大镖客</h5>
            <p>进去看看</p>
        </div>
        <ul>
            <li v-for="hy in livehy" :key="hy.id">
                <h3><img :src="hy.src"></h3>
                <p>
                    <span>{{hy.zz}}</span>
                    <span>{{hy.people}}</span>
                </p>
                <h5>{{hy.tit}}</h5>
            </li>
        </ul>
    </article>
    <!-- 第五人人格 -->
    <article class="live-list">
        <div class="header">
            <h5>第五人格</h5>
            <p>进去看看</p>
        </div>
        <ul>
            <li v-for="d5 in lived5" :key="d5.id">
                <h3><img :src="d5.src"></h3>
                <p>
                    <span>{{d5.zz}}</span>
                    <span>{{d5.people}}</span>
                </p>
                <h5>{{d5.tit}}</h5>
            </li>
        </ul>
    </article>
    <!-- 王者荣耀 -->
    <article class="live-list">
        <div class="header">
            <h5>王者荣耀</h5>
            <p>进去看看</p>
        </div>
        <ul>
            <li v-for="wz in livewz" :key="wz.id">
                <h3><img :src="wz.src"></h3>
                <p>
                    <span>{{wz.zz}}</span>
                    <span>{{wz.people}}</span>
                </p>
                <h5>{{wz.tit}}</h5>
            </li>
        </ul>
    </article>
</main>
</template>

<script>
import Axios from 'axios'

export default {
    data(){
        return {
            livedt: [],
            livear:[],
            livehy:[],
            lived5:[],
            livewz:[]
        }
    },
    mounted(){
        let url = './static/data/livelist.json'

        Axios.get(url).then(res => {
            this.livedt= res.data.liveDatadt
        }).catch()
    
        
        Axios.get(url).then(res=>{
            this.livear=res.data.liveDataar
        }).catch()

        Axios.get(url).then(res=>{
            this.livehy=res.data.liveDatahy
        }).catch()

        Axios.get(url).then(res=>{
            this.lived5=res.data.liveDatad5
        }).catch()

        Axios.get(url).then(res=>{
            this.livewz=res.data.liveDatawz
        }).catch()
    }   
}
</script>

<style scoped>
main{
    padding: 1rem 0 1.2rem;  
    background: #fff; 
}
/* live-btn */
main .live-btn{
    display: flex;
    flex-flow: row wrap;
}
main .live-btn li{
    width: 25%;
    text-align: center;
    padding: 0.2rem 0;
}
main .live-btn li img{
    height: 1rem;
}
/* list */
main .live-list{
    border-top: .15rem solid rgb(233, 233, 233);
    padding: 0.24rem;
}
main .live-list .header{
    display: flex;
    justify-content: space-between;
    padding-bottom: 0.24rem;
    font-size: .25rem;
}
main .live-list .header p{
    color: rgb(187, 187, 187);
}
main .live-list ul{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
}
main .live-list ul li{
    width: 2.8rem;
}
main .live-list ul li:hover img{
    opacity: 1;
}
main .live-list ul li h3{
    border-radius: .1rem;
    width: 2.8rem;
    background: #000;
}
main .live-list ul li img{
    width: 2.8rem;
    opacity: .6;
}
main .live-list ul li p{
    display: flex;
    justify-content: space-between;
    font-size: .24rem;   
    color: #fff;
    padding: 0 .1rem;
    margin-top: -0.42rem;
}
main .live-list ul h5{
    padding: 0.1rem;
    font-size: .26rem;
    text-overflow: ellipsis;
    overflow: hidden;
    height: 0.46rem;
}
</style>




