<template>
    <main>
      <header>
      <ul class="header">
          <li><router-link to=""><img src="../../assets/img/header_list.jpg" alt=""/></router-link></li>
          <li><input type="text"/></li>
          <li><a href="#"><img src="../../assets/img/header_down.jpg" alt=""/></a></li>
          <li><a href="#"><img src="../../assets/img/header_letter.jpg" alt=""/></a></li>
      </ul>
      </header>
      <nav>
        <ul class="nav">
          <li><a href="#">直播 <span></span></a></li>
          <li><a href="#" class="pink">推荐 <span></span></a></li>
          <li><a href="#">追番 <span></span></a></li>
          <li><a href="#">英雄联盟 <span></span></a></li>
          <li><a href="#">萌战<span></span></a></li>
          <li><a href="#">故事王<span></span></a></li>
          <li><a href="#">进览会<span></span></a></li>
        </ul>
      </nav>
      <div class="banner">
        <ul class="b-list">
            <li>综合</li>
            <li><img src="../../assets/img/phb.jpg"><router-link to="" >排行榜</router-link> </li>
        </ul>
        <swipe class="my-swipe">
            <swipe-item class="slide1"><img src="../../assets/img/banner1.jpg" alt=""></swipe-item>
            <swipe-item class="slide2"><img src="../../assets/img/banner2.jpg" alt=""></swipe-item>
            <swipe-item class="slide3"><img src="../../assets/img/banner3.jpg" alt=""></swipe-item>
            <swipe-item class="slide4"><img src="../../assets/img/banner4.jpg" alt=""></swipe-item>
        </swipe>
      </div>
      <ul class="movie-list" >
            <li @click="goDetail(movie.id)" class="movie" v-for="movie in movieList" :key="movie.id" >
                <div class="movie-img"><img :src="movie.src" /></div>
                <div class="movie-msg">{{movie.msg}}</div>
                <div class="movie-info">
                  <p>
                    <router-link to="" >{{movie.btn}}</router-link>
                    <router-link to="" >{{movie.nm}}</router-link>
                  </p>
                  <img src="../../assets/img/three_gray.jpg" alt="">
                </div>
            </li>
            <div class="loading" v-show = "loadingShow">
              <img src="../../assets/img/loading.gif">
            </div> 
            <div class="tip" v-show="tip">
              <h4>--你已到达世界尽头!--</h4>  
            </div>         
        </ul>          
    </main>
</template>

<script>
import Axios from "axios";
import { Swipe, SwipeItem } from 'vue-swipe';

export default {
    components: {
        'swipe': Swipe,
        'swipe-item': SwipeItem
    },
  data() {
    return {
      movieList: [], //接收来自api的数据 或 分页数据
      loadingShow: false, //加载动画
      tip: false, //<h4>--你已到达世界尽头!--</h4>
    }
  },
  methods:{
    loadData(){
      let url = "./static/data/bilidata.json"; //url => 本地数据.

      Axios.get(url)
        .then(res => {
          let list = res.data.data.movies;
          let data = list.slice(
                this.movieList.length, 
                this.movieList.length +4 ); //获取api数据.
          
          if(data.length < 3){ //最后一页如果不足<5或0时,为"世界尽头"
            this.tip = true;
          }
          this.movieList = this.movieList.concat(data);
        })
        .catch(() => {
          alert("数据接收失败!");
        });
    },
    goDetail(movieId){//跳页
      this.$router.push('/movie/movieDetail/' + movieId);
    }
  },
  mounted(){
    this.loadData();
     // window.onload = function(){ //注意: gif加载无法实现
    window.onscroll = () => {
      this.loadData();
      let clientHeight = document.documentElement.clientHeight;//可视高度
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;; //动态高度
      let scrollHeight = document.documentElement.scrollHeight; //整体高度
      //console.log(clientHeight, scrollTop, scrollHeight);
      if( scrollTop+clientHeight >= scrollHeight-110){ //110是底部导航的高度.
       // console.log('这里是世界尽头: '+this.tip, this.loadingShow);
        this.loadingShow = true; //当前页数据获取完毕后加载动画
        if(!this.tip){
          this.loadData();   
        }else{
          this.loadingShow = false;
        }
      }
    }
  } 
};


</script>

<style scoped>

.pink{
    color: #FB7299!important;
}
.pink span{
    background: #FB7299;
    width: 40px;
    height: 3px;
    display: block;
    margin: 0 auto;
}
main{
    background: #F4F4F4;
    border-top: 1px solid #E7E7E7;
    padding: 0 0 1.2rem;
}
/* --------yetou-------- */
header{
    background: #FB7299;
    padding-right: 0.2rem;
}
.header{
    height: 1rem;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
}
.header li input{
    height: .6rem;
    width: 3.2rem;
    line-height: .6rem;
    border-radius: .3rem;
    padding-left: .44rem;
    border: 0;
    font-size: .26rem;
    background: #E5688C url("../../assets/img/header_search.jpg") no-repeat .1rem center; 
    background-size: .3rem;
}
.header img{
    height: 0.6rem;
}
/* ------------------------nav------------------- */
nav{
  display: flex;
  flex-flow: row nowrap;
  background: #ffffff;
  border-bottom: 1px solid #ccc;
  overflow: auto;
  padding: 0 .2rem;
}
nav::-webkit-scrollbar{display: none;}
.nav{  
    width: 500%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.nav li{
    text-align: center;
    line-height: .8rem;
    width: 1.2rem;font-size: .26rem;
}
.nav li a{
    color: #878787;
}
.nav li a:hover{
    color: #FB7299;
}
.nav li a:hover span{
    background: #FB7299;
    width: 1rem;
    height: 3px;
    display: block;
    margin: 0 auto;
}
/* 轮播图 */
.banner{
  padding: 0 .2rem;
}
.banner .b-list{
  width: 100%;
  height: .8rem;
  display: flex;
  flex-flow: row-nowrap;
  justify-content: space-between;
  align-items: center;font-size: .26rem;
}
.banner .b-list img{width: .5rem;}
.banner .b-list a{
  margin-left: 0.1rem;
  color: #000;
}
.banner .b-list a.router-link-active{
  margin-left: 0.1rem;
  color: #FB7299;
}
.my-swipe {
  height: 2rem;
}
.my-swipe img{
  width: 100%;
}

/* ------------列表 */
.movie-list {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  margin-left: 0.25rem;
}
.movie{
  width: 2.8rem;
  margin-right: 0.25rem;
  margin-bottom: .2rem;
  background: #ffffff;
}
.movie-img img{
  width: 100%;
}
.movie-msg{
  font-size: .26rem;
  padding: 0.13rem;
  color: #272727;
  line-height:.5rem;
  height: .8rem;
  text-overflow: ellipsis;
  overflow: hidden;
}
.movie-info{
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  padding: .13rem;
}
.movie-info a{
  color: #AAAAAA;
  font-size: .24rem;
}
.movie-info img{
  width: .1rem;
}

.loading {
  display: block;
  width: 100%;
  text-align: center;
  margin-bottom: 1rem;
}
.tip{
  text-align: center;
  display: block;
  width: 100%;
}

</style>






