import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Movie from '@/components/movie/Movie'
import MovieList from '@/components/movie/MovieList'
import MovieDetail from '@/components/movie/MovieDetail'
import Chanal from '@/components/chanal/Chanal'
import ChanalList from '@/components/chanal/ChanalList'
import Trend from '@/components/trend/Trend'
import TrendList from '@/components/trend/TrendList'
import Live from '@/components/live/Live'
import LiveList from '@/components/live/LiveList'

Vue.use(Router) 

export default new Router({
  //mode: 'history', //去除URL中的"#"
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect: '/movie/movieList'
    }, {
      path: '/movie',
      name: 'Movie',
      component: Movie,
      children: [
        {
          path: 'movieList',
          name: 'MovieList',
          component: MovieList
        }, {
          path: 'movieDetail/:movieId',
          name: 'MovieDetail',
          component: MovieDetail,
        }
      ]
    }, {
      path: '/chanal',
      name: 'Chanal',
      component: Chanal,
      redirect: '/chanal/chanalList',
      children: [{
        path: 'chanalList',
        component: ChanalList
      }]
    },{
      path: '/trend',
      name: 'Trend',
      component: Trend,
      redirect:'/trend/trendList',
      children:[{
        path:"trendList",
        component:TrendList
      }]
    },{
      path: '/live',
      name: 'Live',
      component: Live,
      redirect: '/live/LiveList',
      children: [{
        path: 'liveList',
        component: LiveList,
      }]
    },
  ]
})



