<style scoped>
main{
    padding: 1rem 0 1.2rem;
    background: #fff;
}
/* -------nav+图标-------- */
.nav_list,.bottom_list{
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    padding-left: .2rem;
    border-bottom: 1px solid #ccc;
    margin-top: 0.2rem;
}
.nav_list li,.bottom_list li{
    margin-right: .2rem;
    margin-bottom: .2rem;
    width: 1.32rem;
    text-align: center;
}
.nav_list li img,.bottom_list li img{
    width: 70%;
}
.nav_list li p,.bottom_list li p{
    color: #575757;
    font-size: .24rem;
}
.wdpd,.fxpd{
    border-bottom: 1px solid #ccc;
}
.wdpd_nav,.fxpd_nav{
    display: flex;
    justify-content: space-between;
    margin: 0 .2rem;
    font-size: .36rem;
    font-weight: bold;
    line-height: .9rem;
}
.wdpd_nav a,.fxpd_nav a{
    font-weight: 400;
    color: #A4A4A4;
    font-size: .26rem;
}
.wdpd_msg{
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    margin-left: 0.31rem;
}
.wdpd_msg li{
    width: 1.7rem;
    margin-right: 0.31rem;
    margin-bottom: .3rem;
    text-align: center;
    background: #e8e8e8;
    color: #636363;
    font-size: .24rem;
    line-height: .6rem;
    border-radius: .3rem;
}
.wdpd_msg li a{
    color: #636363;
}
.fxpd_msg{
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-evenly;
    margin: 0 0 .24rem 0.2rem;
}
.fxpd_msg li{
    border: 1px solid #ccc;
    margin-right: 0.2rem;
    padding: 0.2rem;
    text-align: center;
}
 .fxpd_msg li img{
    width: 100%;
}
.fxpd_msg li h5{
    color: #4A4A4A;
    text-overflow: ellipsis;
    overflow: hidden;
    height: 0.46rem;
    font-size: .26rem;
    line-height: .48rem;
}
.fxpd_msg li p{
    color: #A4A4A4;
    font-size: .22rem;
    padding-bottom: 0.1rem;
}
.fxpd_msg li button{
    color: #4A4A4A;
    width: 100%;
    height: 0.48rem;
    color: #FB7299;
    border: 1px solid #fb7299;
    background: #fff;
    font-size: .26rem;
} 
</style>

<template>
   <main>
       <ul class="nav_list">
           <li v-for="chanal in chanalList" :key="chanal.id">
               <router-link to="">
                   <img :src="chanal.bg" />
                   <p>{{chanal.name}}</p>
               </router-link>           
           </li>
       </ul>
       <div class="wdpd">
           <ul class="wdpd_nav">
               <li>我的频道</li>
               <li><router-link to="" >全部></router-link></li>
           </ul>
           <ul class="wdpd_msg">
               <li v-for="wdpd in wdpdList" :key="wdpd.id">
                    <router-link to="">
                        <p>{{wdpd.msg}}</p>
                    </router-link>                    
               </li>
           </ul>
       </div>
       <div class="fxpd">
           <ul class="fxpd_nav">
               <li>发现频道</li>
               <li><router-link to="" >全部></router-link></li>
           </ul>
           <ul class="fxpd_msg">
               <li v-for="fxpd in fxpdList" :key="fxpd.id">
                    <router-link to="">
                        <img :src="fxpd.img" />
                        <h5>{{fxpd.img_tit}}</h5>
                        <p>{{fxpd.img_peo}}</p>
                        <button>{{fxpd.img_btn}}</button>
                    </router-link>                    
               </li>
           </ul>
       </div>
        <ul class="bottom_list">
           <li v-for="bottom in pdList" :key="bottom.id">
               <router-link to="">
                   <img :src="bottom.src" />
                   <p>{{bottom.btn}}</p>
               </router-link>           
           </li>
       </ul>
   </main> 
</template>

<script>
import Axios from 'axios'

export default {
    data(){
        return {
            chanalList: [],
            wdpdList:[],
            fxpdList:[],
            pdList:[]
        }
    },
    mounted() {
       let url = "./static/data/chanallist.json"

       Axios.get(url).then(res => {
           this.chanalList = res.data.albums;
           console.log(res, this.chanalList);
       }).catch(); 

       Axios.get(url).then(res => {
           this.wdpdList = res.data.wdpd;
           console.log(res, this.wdpdList);
       }).catch(); 

       Axios.get(url).then(res => {
           this.fxpdList = res.data.fxpd;
           console.log(res, this.fxpdList);
       }).catch(); 

       Axios.get(url).then(res => {
           this.pdList = res.data.pd;
           console.log(res, this.pdList);
       }).catch(); 

    }
}
</script>



