<style scoped>
main{
    background: #fff;
}
.header{
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 9;
}
.header nav ul{
    width: 100%;
    display:flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    position: absolute;
    top: 0;
    left: 0;
}
.header nav ul li{
    padding: .24rem;
}
.header nav ul li img{
    width: .4rem;
}
main .head:hover+nav{
    opacity: 0;
}
main .head video{
    width: 100%;
    background: #000;
}
main .head p{
    background: #333333;
    padding: .12rem .4rem;
    margin-top: -0.155rem;
    box-shadow: 0 .06rem .2rem #888;
}
main .head input{
    width: 100%;
    height: .55rem;
    background: #5A5A5A;
    border-radius: .27rem;
    border: 0;
    padding-left: .2rem;
    font-size: .28rem;
}
/* ------主内容---------- */
.header .nav_list{
    background: #fff;
    box-shadow: 0 .06rem .2rem #d4d4d4;
    height: .7rem;
    display: flex;
    justify-content: space-around;
}
.header .nav_list li{
    width: 50%;
    font-size: .28rem;
    text-align: center;
    color: #767676;
    padding-top: 0.13rem;
}
.header .nav_list li:hover{
    color: #FB7299;
}
.header .nav_list li:hover span{
    background: #FB7299;
    width: .7rem;
    height: 2px;
    display: block;
    margin: .06rem auto;
}
/* ---------------作者------------- */
article{
    margin: 4.9rem 0 1.4rem;
}
article .author{
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    padding:.24rem;
}
article .author li{
    color: #4F4F4F;
    display: flex;
    font-size: .3rem;
}
article .author li img{
    border-radius: 50%;
    width: .88rem;
    height: 0.88rem;
    margin-right: .22rem;
}
article .author li span{
    display: block;
    color: #999;
    font-size: .26rem;
}
article .author li:hover{
    color: #FB7299;
}
article .author li:last-child{
    background: #FB7299;
    color: #fff;
    display: block;
    width: 1.4rem;
    height: .54rem;
    border-radius: 5px;
    text-align: center;
    line-height: .54rem;
    font-size: .28rem;
    margin-top: .24rem;
}
/* ------------视频介绍--------------- */
article .work{
    padding:0 .24rem;
}
article .work li{
    color: #A4A4A4;
    font-size: .28rem;
    display: flex;
    justify-content:flex-start;
}
article .work li img{
    width: .4rem;
    height: 0.33rem;
}
article .work li h3{
    color: #707070;
    font-size: .32rem;
    height: 0.65rem;
    overflow: hidden;
}
article .work li>img{
    padding-top: .1rem;
}
article .work li p{
    padding-right: 0.3rem;
}
article .work li p img{
    padding-top: 0.06rem;
}
article .work li p span{
    padding-left: 0.1rem;
}
article .work #msg{
    display: none;
}
/* ----------------label--------------------- */
article .label{
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    text-align: center;
    padding: 0.25rem 0 .2rem .5rem;
    border-bottom: 1px solid rgb(224, 224, 224);
}
article .label li{
    padding-right: 0.4rem;

}
article .label li img{
    width: .6rem;
}
article .label li span{
    display: block;
    padding: 0.1rem 0;
    color: #B5B5B5;
    font-size: .29rem;
}
/* ---------conment-btn------------- */
article .conment_btn{
    padding: 0.24rem .45rem 0 .24rem;
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    position: relative;
    height: 0.6rem;
    overflow: hidden;
}
article .conment_btn li{
    margin-right: 0.24rem;
    font-size: .26rem;
    margin-bottom: .24rem;
    text-align: center;
    background: #e8e8e8;
    color: #636363;
    line-height: .6rem;
    border-radius: .3rem;
    padding: 0 .2rem;
}
article .conment_btn .right-top{
    position: absolute;
    right: .25rem;
    top: .35rem;
}

/* -----------movie-list--------------- */

article .movie_list li{
    display: flex;
    color: #ADADAD;
    border-bottom: 1px solid rgb(226, 226, 226);
    font-size: .24rem;
    padding: 0.24rem;
}
article .movie_list li>img{
    display: inline-block;
    padding-right: 0.24rem;
    width: 2.3rem;
    height: 1.84rem;
}
article .movie_list li h3{
    color: #4A4A4A;
    font-size: .26rem;
    overflow: hidden;
    height: 1rem;
}
article .movie_list li p img{
    width: 0.38rem;
    height: 0.28rem;
    padding: 0.07rem .09rem 0 0;
}
</style>

<template>
    <main>
    <div class="header">
      <div class="head">
        <video :src="movieDetail.video" controls></video>
        <p>
            <input type="text" placeholder="发射弹幕"/>
        </p>
      </div>
      <nav>
        <ul>
          <li>
            <router-link to="/movie/MovieList">
               <img src="../../assets/img/left.png" alt=""/>
            </router-link>
          </li>
          <li>
            <router-link to="/movie/MovieList">
               <img src="../../assets/img/three.png" alt=""/>
            </router-link>                
          </li>
        </ul>
        </nav>
        <ul class="nav_list">
            <li class="pink">简介 <span></span></li>
            <li>评论 <span></span></li>
        </ul>
    </div>
    <article>
         <ul class="author">
            <li>
                <img :src="movieDetail.tu"/>
                <p>{{movieDetail.zz}}<span>{{movieDetail.fs}}粉丝</span></p>
            </li>
            <li>+ 关注</li>
         </ul>
         <ul class="work">
            <li><h3 id="tip">{{movieDetail.tit}}</h3><img src="../../assets/img/bottom.jpg" id="tipbtn"></li>
            <li>
                <p><img src="../../assets/img/work_list1.jpg"><span>{{movieDetail.bfl}}</span></p>
                <p><img src="../../assets/img/work_list2.jpg"><span>{{movieDetail.pj}}</span></p>
                <p><span>{{movieDetail.data}}</span></p>
            </li>
            <li id="msg">
                <p>{{movieDetail.msg}}</p>
            </li>
         </ul>
         <ul class="label">
             <li><img src="../../assets/img/label1.jpg"><span>{{movieDetail.dz}}</span></li>
             <li><img src="../../assets/img/label2.jpg"><span>{{movieDetail.cp}}</span></li>
             <li><img src="../../assets/img/label3.jpg"><span>{{movieDetail.bb}}</span></li>
             <li><img src="../../assets/img/label4.jpg"><span>{{movieDetail.sc}}</span></li>
             <li><img src="../../assets/img/label5.jpg"><span>{{movieDetail.zf}}</span></li>
         </ul>
         <ul class="conment_btn">
            <span class="right-top"><img src="../../assets/img/bottom.jpg"></span>
            <li v-for="moviebtn in movieDetailBtn" :key="moviebtn.id">{{moviebtn.msg}} </li>
         </ul>

         <ul class="movie_list">
             <li v-for="list in movieList" :key="list.id">
                <img :src="list.img">
                <div>
                    <h3>{{list.tit}}</h3>
                    <p><img src="../../assets/img/up.jpg">{{list.zz}}</p>
                    <p>
                        <img src="../../assets/img/work_list1.jpg"><span>{{list.bfl}}</span>
                        <img src="../../assets/img/work_list2.jpg"><span>{{list.pj}}</span>
                     </p>
                </div>
             </li>
         </ul>
      </article>
    </main>
</template>

<script>
import Axios from 'axios'

export default {
    data(){//此处必须使用data()方法定义数据
        return {
            movieDetail: {},
            movieDetailBtn:{},
            movieList:{}
        }
    },
    mounted() {
        let url = './static/data/moviedetail'+ this.$route.params.movieId  +'.json'
        Axios.get(url).then(res => {
            this.movieDetail = res.data.data.MovieDetailModel;
        }).catch();

        Axios.get(url).then(res => {
            this.movieDetailBtn = res.data.data.MovieDetailBtn;
           console.log(res, this.movieDetailBtn);
        }).catch();

        Axios.get(url).then(res => {
            this.movieList= res.data.data.MovieDetailList;
        }).catch();

        let TipBtn=document.getElementById("tipbtn");
        let Tip=document.getElementById("tip");
        let Msg=document.getElementById("msg");
        let num=0; 
        TipBtn.onclick=function(){
            num++;
            if(num%2==1){
                Msg.style.display="block";
                Tip.style.height="1.3rem"
                Tip.style.transition=".5s all";
            }else{
                Msg.style.display="none"
                Tip.style.height=".65rem"
                Tip.style.transition=".5s all";
            }
        }


        let Conbtn=document.getElementsByClassName("right-top")[0];
        let Conmsg=document.getElementsByClassName("conment_btn")[0];
        let sum=0;
        Conbtn.onclick=function(){
            sum++;
            if(sum%2==1){
                Conmsg.style.height="1.5rem"
                Conmsg.style.transition=".5s all";
            }else{
                Conmsg.style.height=".6rem"
                Conmsg.style.transition=".5s all";
            }
        }
    }
}
</script>




