<template>
<main>
    <p><input type="text" placeholder="查找精彩动态内容"></p>
    <ul class="trend_btn">
        <li>#shinhwa#</li>
        <li>#电影#</li>
        <li>#游戏#</li>
        <li>我关注的话题</li>
    </ul>
    <ul class="trend_list">
        <li v-for="trend in trendList" :key="trend.id">
            <div class="list_tit">
                <header>
                  <img :src="trend.tu">
                  <h5>{{trend.tit}}<span>{{trend.date}}</span></h5>
                </header>
                <img src="../../assets/img/three_gray.jpg">
            </div>
            <div class="list_msg">
                <p>{{trend.msg}}</p>
                <img :src="trend.img">
            </div>
            <div class="list_btn">
                <ul>
                    <li><img src="../../assets/img/trend_btn1.png" alt=""></li>
                    <li><img src="../../assets/img/trend_btn2.png" alt=""></li>
                    <li><img src="../../assets/img/trend_btn3.png" alt=""></li>
                </ul>
            </div>
        </li>
    </ul>
            <div class="loading" v-show = "loadingShow">
              <img src="../../assets/img/loading.gif">
            </div> 
            <div class="tip" v-show="tip">
              <h4>--你已到达世界尽头!--</h4>  
            </div>  
</main>
</template>

<script>
   import Axios from 'axios'

export default {
  data() {
    return {
      trendList: [],
      loadingShow: false, //加载动画
      tip: false, //<h4>--你已到达世界尽头!--</h4>
    }
  },
  methods:{
    loadData(){
        let url = "./static/data/trendlist.json"

      Axios.get(url)
        .then(res => {
          let list = res.data.data.trend;
          let data = list.slice(
                this.trendList.length, 
                this.trendList.length +3 ); //获取api数据.
          
          if(data.length < 3){ //最后一页如果不足<5或0时,为"世界尽头"
            this.tip = true;
          }
          this.trendList = this.trendList.concat(data);
        })
        .catch(() => {
          alert("数据接收失败!");
        });
    }
  },
  mounted(){
    this.loadData();

    window.onscroll = () => {
      this.loadData();
      let clientHeight = document.documentElement.clientHeight;//可视高度
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;; //动态高度
      let scrollHeight = document.documentElement.scrollHeight; //整体高度
      if( scrollTop+clientHeight >= scrollHeight-110){ //110是底部导航的高度.
        this.loadingShow = true; //当前页数据获取完毕后加载动画
        if(!this.tip){
          this.loadData();   
        }else{
          this.loadingShow = false;
        }
      }
    }
  } 
};

 
</script>

<style scoped>
.loading {
  display: block;
  width: 100%;
  text-align: center;
  margin-bottom: 1rem;
}
.tip{
  text-align: center;
}
main{
    padding: 1rem 0 1.3rem;
    background: #fff;
}
/* -----------搜索框---------------- */

main>p{
    border-bottom: 1px solid rgb(223, 222, 222);
}
main p input{
    margin: .24rem .24rem;
    height: .6rem;
    width: 5.4rem;
    border-radius: .3rem;
    padding-left: .5rem;
    border: 0;
    font-size: .3rem;
    background: #f3f2f2 url("../../assets/img/header_search_gray.jpg") no-repeat .1rem center; 
    background-size: .5rem;
}
/* --------------频道-------------------- */
main .trend_btn{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    margin: 0.2rem .24rem;
    text-align: center;
}
main .trend_btn li{
    width: 49%;
    padding: 0.1rem 0;
    color: #4776ca;
    font-size: .3rem;
}
main .trend_btn li:nth-of-type(1){
    border-right: 1px solid rgb(223, 222, 222);
    border-bottom: 1px solid rgb(223, 222, 222);
}
main .trend_btn li:nth-of-type(4){
    border-top: 1px solid rgb(223, 222, 222);
    border-left: 1px solid rgb(223, 222, 222); 
}
/* --------------动态----------- */
main .trend_list>li{
    border-top: .15rem solid rgb(236, 236, 236);
    padding: 0.24rem;
}
main .trend_list .list_tit{
    display: flex;
    justify-content: space-between;
}
main .trend_list .list_tit header{
    display: flex;
    justify-content: flex-start;
}
main .trend_list .list_tit header img{
    border-radius: 50%;
    width: 1rem;
    height: 1rem;
    border: 1px solid #ececec;
}
main .trend_list .list_tit header h5{
    padding: 0.1rem .2rem;
    color: #E5688C;
    font-size: .3rem;
    font-weight: bold;
}
main .trend_list .list_tit header h5 span{
    display: block;
    color: #ccc;
    font-size: .26rem;
    font-weight: 400;
}
main .trend_list .list_tit>img{
    margin-top: .3rem;
    width: 0.12rem;
    height: 0.35rem;
}
main .trend_list .list_msg img{
    width: 5rem;
}
main .trend_list .list_msg p{
    padding: 0.1rem 0;
}
main .trend_list .list_btn ul{
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-evenly;
    padding: 0.3rem 0 0;
}
main .trend_list .list_btn ul li{
    width: 33%;
    text-align: center;
}
main .trend_list .list_btn ul li img{
    width: .6rem;
}
</style>



