<template>
  <div class="top">
    <header>
    <ul class="header">
        <li><router-link to=""><img src="../../assets/img/header_list.jpg" alt=""/></router-link></li>
        <li><slot name="title"></slot></li>
        <li><a href="#"><img src="../../assets/img/search.jpg" alt=""/></a></li>
        <li><a href="#"><img src="../../assets/img/header_letter.jpg" alt=""/></a></li>
    </ul>
    </header>
  </div>
</template>

<script>
export default { 
  methods:{
    goHome(){
      this.$router.push('/')
    }
  },
  props: ['bg']
};
</script>

<style scoped>

.top{
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 99;
}
header{
    background: #FB7299;
    padding-right: 0.2rem;
}
.header{
    height: 1rem;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
}
.header li:nth-of-type(2){
    width: 3.2rem;
}
.header img{
    height: 0.6rem;
}
</style>




